.product {
  cursor: pointer;

  @include hover (transform, scale(1.1));

  padding-bottom: 40px;

  @include on-tablet {
    padding-bottom: 56px;
  }

  @include on-desktop {
    padding-bottom: 0;
  }

  &__photo {
    width: 100%;
    padding-bottom: 24px;
  }

  &__title {
    margin: 0;
    font-size: 18px;
    font-weight: 700;
    line-height: 25.2px;

    padding-bottom: 8px;
  }

  &__info {
    margin: 0;
    font-family: $secondary-font;
    font-size: 16px;
    font-weight: 400;
    line-height: 22.4px;
    max-width: 280px;

    padding-bottom: 12px;
  }

  &__price {
    margin: 0;
    font-size: 16px;
    font-weight: 700;
    line-height: 22.4px;
  }
}
